                  >
                    Dispatched to {{ load.data.driver }} {{ load.data.driver_phone ? `| Phone: ` : '' }}
                    <a
                      v-if="load.data.driver_phone"
                      :href="`tel:${load.data.driver_phone}`"
                    >
                      {{ load.data.driver_phone }}
                    </a>
                  </p>

                  <p
                    v-if="load.data.invoice && load.data.status && (load.data.status.toLowerCase() === $constants.LOAD_STATUSES.BILLED)"
                    class="ma-0 mt-3 mr-7 font-weight-medium body-2 mb-0 mr-7 success--text text--darken-1"
                  >
                    Invoice is sent on
                    {{ load.data.invoice && load.data.invoice.created.split(' ')[0] | formatDate }} {{ load.data.invoice.created.split(' ')[1] }}
                  </p>

                  <p
                    v-if="load.data.payment_received_date"
                    class="ma-0 mt-3 mr-7 font-weight-medium body-2 mb-0 mr-7 success--text text--darken-1"
                  >
                    Payment is received on
                    {{ load.data.payment_received_date.split(' ')[0] | formatDate }} {{ load.data.payment_received_date.split(' ')[1] }}
                  </p>

                  <p
                    v-if="load.data.status && (load.data.status.toLowerCase() === $constants.LOAD_STATUSES.BILLED)"
                    class="ma-0 mt-3 mr-7 font-weight-medium body-2 mb-0 mr-7 load-card-grey--text"
                  >
                    No payment received
                  </p>

                  <p
                    v-if="load.data.status && (load.data.status.toLowerCase() === 'past due')"
                    class="ma-0 mt-3 mr-7 font-weight-medium body-2 mb-0 mr-7 error--text"
                  >
                    Payment is past due
                  </p>
                </v-col>
              </v-row>

              <v-row class="mb-md-11 mb-6">
                <v-col
                  cols="12"
                  class="py-0"
                >
                  <div class="info-panel">
                    <div
                      class="info-panel-header pt-2 pr-5 pb-2 pl-5 d-flex justify-space-between align-center"
                    >
                      <h3 class="font-weight-semi-bold body-1 mr-7">
                        Internal Notes
                      </h3>

                      <v-btn
                        depressed
                        class="primary--text"
                        :min-width="100"
                        @click="modalOpened = true"
                      >
                        {{ load.data.internal_notes ? 'Edit ' : 'Add ' }} notes

                        <Modal
                          :modal-opened="modalOpened"
                          @modal:close="closeInternalNotesModal"
                        >
                          <h3 class="pb-3 primary-dark--text">
                            Add a&nbsp;new internal note to&nbsp;the&nbsp;order
                          </h3>

                          <v-textarea
                            outlined
                            rows="6"
                            row-height="20"
                            shaped
                            class="textarea"
                            v-model="internal_notes"
                          />

                          <div class="d-flex justify-end">
                            <v-btn
                              depressed
                              class="primary-text mr-2"
                              @click="closeInternalNotesModal"
                            >
                              Close
                            </v-btn>
                            <v-btn
                              depressed
                              class="font-weight-semi-bold"
                              color="primary"
                              @click="updateInternalNotes"
                            >
                              Save
                            </v-btn>
                          </div>
                        </Modal>
                      </v-btn>
                    </div>
                    <div class="pa-5 info-panel-item">
                      <p
                        class="ma-0 mr-7 font-weight-medium"
                        :class="{ 'load-card-grey--text': !load.data.internal_notes }"
                      >
                        {{ load.data.internal_notes ? load.data.internal_notes : 'Add your first internal note for this load' }}
                      </p>
                    </div>
                  </div>
                </v-col>
              </v-row>

              <v-row v-if="load.activities.length">
                <v-col
                  cols="12"
                  class="py-0"
                >
                  <panel-dropdown
                    title="Activities"
                    expand
                  >
                    <v-list-item
                      class="panel-activities-item pa-5"
                      v-for="activity in load.activities"
                      :key="activity.activityDate"
                    >
                      <v-list-item-content>
                        <ul>
                          <li>
                            {{ activity.title }}
                          </li>
                          <li
                            v-if="activity.created"
                            class="body-2 load-card-grey--text"
                          >
                            {{ activity.created && activity.created.split(' ')[0] | formatDate }} {{ activity.created && activity.created.split(' ')[1] }}
                          </li>
                        </ul>
                      </v-list-item-content>
                    </v-list-item>
                  </panel-dropdown>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-container>
      </template>
      <template v-else>
        <page-not-found-content class="page-wrapper" />
      </template>
    </template>
  </base-layout>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import * as constants from '@modules/loads/constants';
import Modal from '@components/Modal/Modal.vue';
import PhotoInspection from '@components/Loads/PhotoInspection/PhotoInspection.vue';
import StatusLoad from '@components/Loads/StatusLoad/StatusLoad.vue';
import PanelDropdown from '@components/Loads/PanelDropdown/PanelDropdown.vue';
import BaseLayout from '@components/BaseLayout';
import { actions as driversActions } from '@modules/drivers/constants';
import formatDate from '@utils/filters/formatDate';
import PageNotFoundContent from '@components/PageNotFoundContent';

const {
  mapActions: mapLoadsActions,
  mapState: mapLoadsState,
  mapGetters: mapLoadsGetters,
  mapMutations: mapLoadsMutations,
} = createNamespacedHelpers('loads');
const {
  mapActions: mapDriversActions,
  mapState: mapDriversState,
} = createNamespacedHelpers('drivers');

export default {
  name: 'Load',
  components: {
    PanelDropdown,
    StatusLoad,
    Modal,
    PhotoInspection,
    BaseLayout,
    PageNotFoundContent,
  },
  filters: { formatDate },
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  data: () => ({
    modalOpened: false,
    internal_notes: null,
  }),
  computed: {
    ...mapLoadsState(['load', 'inspections', 'query']),
    ...mapLoadsGetters({
      invoiceLink: constants.getters.INVOICE_LINK,
      receiptLink: constants.getters.RECEIPT_LINK,
      bolLink: constants.getters.BOL_LINK,
    }),
    ...mapDriversState(['drivers']),
  },
  methods: {
    updateStatus(nextStatus) {
      this.updateLoadStatus({ loadId: this.id, nextStatus })
        .then(() => this.$notificationSuccess('Load status was updated'))
        .catch((error) => {
          if (error.response.status === 400) {
            this.$notificationError(error.response.data[0]);
          } else {
            this.$notificationError('Load data wasn\'t updated');
          }
        });
    },
    closeInternalNotesModal() {
      this.modalOpened = false;
      this.internal_notes = this.load.data.internal_notes;
    },
    updateInternalNotes() {
      const nextInternalNotes = { internal_notes: this.internal_notes };
      this.updateLoadData({ id: this.id, data: nextInternalNotes })
        .then(() => {
          this.$notificationSuccess('Internal notes was updated');
          this.updateLoadPropData({ prop: 'data', data: nextInternalNotes });
          this.closeInternalNotesModal();
        })
        .catch(() => this.$notificationSuccess('Internal notes was not updated'));
    },
    ...mapLoadsActions({
      getLoad: constants.actions.GET_LOAD,
      getLoadAttachments: constants.actions.GET_LOAD_ATTACHMENTS,
      dispatchToDriver: constants.actions.DISPATCH_LOAD_TO_DRIVER,
      getLoadVehicleInspections: constants.actions.GET_LOAD_VEHICLE_INSPECTIONS,
      updateLoadData: constants.actions.UPDATE_LOAD_DATA,
      updateLoadStatus: constants.actions.UPDATE_LOAD_STATUS,
      getLoadActivities: constants.actions.GET_LOAD_ACTIVTIES,
    }),
    ...mapLoadsMutations({

    }),
    ...mapDriversActions({
      getDrivers: driversActions.GET_DRIVERS,
    }),
  },
  mounted() {
    // TODO: log error - console.log(this.query);
    this.getDrivers({ query: '?status=active' });
    this.getLoad({ id: this.id })
      .then(() => {
        this.internal_notes = this.load.data.internal_notes;
        this.getLoadAttachments({ id: this.id });
        this.getLoadVehicleInspections({ loadId: this.id });
        this.getLoadActivities({ id: this.id });
      });
  },
};
</script>

