<template>
  <v-chip
    class="status-load"
    label
    pill
    :class="`color-${computedStatus}`"
  >
    <span
      class="font-weight-bold text-capitalize"
      :class="{ 'text-uppercase': uppercase }"
    >{{ status.replace('-', '  ') }}</span>
  </v-chip>
</template>

<script>
export default {
  name: 'StatusLoad',
  props: {
    status: {
      type: String,
      required: true,
    },
    uppercase: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    computedStatus() {
      return this.status.toLowerCase().replace(' ', '-');
    },
  },
};
</script>

