<template>
  <div>
    <v-container
      v-for="(inspection, index) in inspections"
      :key="index"
      class="pa-0 mb-4 mt-10"
      fluid
    >
      <h3
        v-if="hasPhotos(inspection)"
        class="primary-dark--text font-weight-semi-bold mb-2"
      >
        {{ inspection.vehicle.year }} {{ inspection.vehicle.name }}
        <template v-if="inspection.vehicle.vin">
          &ndash; {{ inspection.vehicle.vin }}
        </template>
      </h3>

      <v-expansion-panels v-if="hasPhotos(inspection)">
        <v-expansion-panel v-if="inspection.pickup.photos.length">
          <v-expansion-panel-header>
            Damages photos &ndash; Pick Up Inspection photos
          </v-expansion-panel-header>

          <v-expansion-panel-content>
            <photo-gallery :photos="getPhotos(inspection, 'pickup', 'photos')" />
          </v-expansion-panel-content>
        </v-expansion-panel>

        <v-expansion-panel v-if="inspection.delivery.photos.length">
          <v-expansion-panel-header>
            Damages photos &ndash; Delivery Inspection photos
          </v-expansion-panel-header>

          <v-expansion-panel-content>
            <photo-gallery :photos="getPhotos(inspection, 'delivery', 'photos')" />
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>

      <v-expansion-panels
        v-if="hasSourcePhotos(inspection)"
        class="mt-4"
      >
        <v-expansion-panel v-if="inspection.pickup.source_photos.length">
          <v-expansion-panel-header>
            Source photos &ndash; Pick Up Inspection photos
          </v-expansion-panel-header>

          <v-expansion-panel-content>
            <photo-gallery :photos="getPhotos(inspection, 'pickup', 'source_photos')" />
          </v-expansion-panel-content>
        </v-expansion-panel>

        <v-expansion-panel v-if="inspection.delivery.source_photos.length">
          <v-expansion-panel-header>
            Source photos &ndash; Delivery Inspection photos
          </v-expansion-panel-header>

          <v-expansion-panel-content>
            <photo-gallery :photos="getPhotos(inspection, 'delivery', 'source_photos')" />
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>

      <v-btn
        v-if="hasPhotos(inspection) || hasSourcePhotos(inspection)"
        depressed
        :key="index"
        class="mt-5"
        color="success"
        :disabled="disabled"
        @click="() => downloadPhotos(inspection.vehicle.vin, index)"
      >
        {{ loading[index] ? `Archiving photos - ${intervals[index] || '30'}s` : 'Download photos' }}
      </v-btn>
    </v-container>
  </div>
</template>

<script>
import axios from 'axios';
import PhotoGallery from '@components/PhotoGallery/PhotoGallery.vue';

export default {
  name: 'PhotoInspection',
  components: { PhotoGallery },
  props: {
    loadId: {
      type: [String, Number],
      required: true,
    },
    inspections: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      disabled: false,
      loading: [],
      intervals: [],
    };
  },
  methods: {
    hasPhotos({ delivery, pickup }) {
      return delivery.photos.length || pickup.photos.length;
    },
    hasSourcePhotos({ delivery, pickup }) {
      return delivery.source_photos.length || pickup.source_photos.length;
    },
    getPhotos(inspection, inspectionType, photosType) {
      const type = photosType === 'photos' ? 'damages'.toUpperCase() : 'source'.toUpperCase();
      const vin = String(inspection.vehicle.vin).toUpperCase();
      const photos = inspection[inspectionType][photosType];

      return photos.length ? photos.map((url, index) => ({
        url,
        filename: `${vin}-${type}-${index}`,
      })) : [];
    },
    downloadPhotos(vehicleVin, index) {
      this.$set(this.loading, index, true);
      let n = 30;
      let interval = setInterval(() => {
        this.$set(this.intervals, index, n -= 1);
      }, 1000);
      this.disabled = true;

      axios.get(`${process.env.VUE_APP_ARCHIVE_PHOTO_URL}?internal_id=${encodeURIComponent(this.loadId)}&vehicle_vin=${encodeURIComponent(vehicleVin)}`, { timeout: 0 })
        .then((response) => {
          // setTimeout(() => window.open(response.data.url, '_blank'));
          const anchor = document.createElement('a');
          anchor.href = response.data.url;
          // eslint-disable-next-line prefer-destructuring
          anchor.download = response.data.url.split('/').reverse()[0];
          document.body.appendChild(anchor);
          anchor.click();
          document.body.removeChild(anchor);
        })
        .catch((err) => {
          // TODO: log error - console.error(err);
          throw err;
        })
        .finally(() => {
          this.$set(this.loading, index, false);
          this.$set(this.intervals, index, null);
          clearInterval(interval);
          interval = null;
          this.disabled = false;
        });
    },
  },
};
</script>
