<template>
  <div class="d-flex flex-align-center">
    <v-dialog
      v-model="modalOpened"
      :max-width="maxWidth"
      v-bind="$attrs"
      v-on="$listeners"
      @click:outside="close"
      @keydown.esc="close"
    >
      <v-layout class="white d-flex flex-column px-5 py-3">
        <div
          v-if="isCloseButtonExist"
          class="d-flex justify-end"
        >
          <v-btn
            icon
            @click="close"
            class="btn-close"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </div>
        <slot></slot>
      </v-layout>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: 'Modal',
  props: {
    modalOpened: {
      type: Boolean,
      required: true,
    },
    maxWidth: {
      type: String,
      required: false,
      default: '800px',
    },
    isCloseButtonExist: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  data() {
    return {
      isOpened: this.modalOpened,
    };
  },
  methods: {
    close() {
      this.$emit('modal:close');
    },
  },
};
</script>

<style>
.btn-close {
  margin-right: -10px;
}
</style>
